.App {
  display: flex;
  flex-direction: column;
  margin-top: 50px;
}

html, body {
  display: flex;
  flex-direction: column;
  min-height: 99vh;
  width: 100%;
  margin: 0;
  padding: 0;
}
h1 {
  background: none;
}

.form-container {
  display: flex;
  flex-direction: column;
  margin: 10px 40px;
  background: none;
}

.footer {
  display: flex;
  position: fixed;
  left: 0;
  bottom: 0;
  background: #1e130c;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 30px;
}

.footer span {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: 16px;
  background: none;
}
